
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding-top: 35px;
}
html,
body,
div,
input,
ul,
li,
span,
p,table,tr,th,td {
    padding: 0;
    margin: 0;
}

html,
body {
    width: 100%;
    height: 100vh;
}

ul li {
    list-style: none;
}

.logBox {
    width: 100%;
    padding-top: 45px;
}

.logBox ul {
    width: 80%;
    background: #64BDCD;
    margin: auto;
    padding: 10px 0 30px 0;
    border-radius: 5px;
}

.logBox li {
    margin: auto;
    width: 80%;
    height: 30px;
    line-height: 30px;
    margin-bottom: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}

.logBox input {
    /* border: 1px salmon solid; */
    background: #ffffff;
    color: #333333;
    height: 30px;
    line-height: 30px;
    padding: 0 5px;
    border-radius: 3px;
    border: none;
}

.page ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around
}

.page nav {
    background: #E1EEEB;
    height: 35px;
    line-height: 35px;
}

.userList {
    text-align: center;
    width: 100%;
    margin: auto;
}

.userList tr{
    height: 35px;
}

.userList tr:nth-child(odd){
    background: #D5CDB8;
}

.userList tr:nth-child(even){
    background: rgb(194, 228, 171);
}

.userList th{
    border-bottom: 1px slategrey dashed;
}

.logBox input,
.logBox textarea {
    width: 40vw;
    ;
}

.logBox textarea {
    display: block;
    height: auto;
    min-height: 200px;
    border: 1px saddlebrown solid;
}

.logBox button {
    width: 80px;
    height: 32px;
    /* background: #ffffff; */
    border: none;
    border-radius: 5px;
}

.promarry {
    background: #0A99FF;
    color: white;
}

.promarry:hover {
    background: #47A6EA;
    border: none;
}

.promarry:active {
    background: #47A6EA;
    border: none;
}

.logOut {
    color: gray;
    font-size: 14px;
    background: #4D5AE4;
    color: #E1EEEB;
    height: 35px;
    line-height: 35px;
    text-align: left;
    padding: 0 15px;
}

.logOut span {
    float: right;
}

.header{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
    width: 100vw;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    height: 35px;
    line-height: 35px;
    background: #1881CD;
    color: rgb(205, 227, 231)
}

.nav{
    position: absolute;
    left: 0;
    top:0;
    z-index: 98;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.nav ul{
    background: #389BAD;
    padding: 0 10px;
    height: 100vh;
    width: 100px;
    padding-top:40px;
}

.nav li{
    height: 35px;
    line-height: 35px;
    color: rgb(205, 227, 231);
}

h1[data-v-d8ec41bc], h2[data-v-d8ec41bc] {
  font-weight: normal;
}
ul[data-v-d8ec41bc] {
  list-style-type: none;
  padding: 0;
}
li[data-v-d8ec41bc] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-d8ec41bc] {
  color: #42b983;
}
#toast {
    width: 100%;
    position: fixed;
    bottom: 10vh;
    z-index: 99;
    text-align: center;
    font-family: PingFangSC-Medium,PingFangSC-Semibold,PingFangSC-Light,'Avenir', Helvetica, Arial, sans-serif;
}

#toast p {
    margin: auto;
    width: 80%;
    max-width: 400px;
    background: #2B2B2B;
    color: antiquewhite;
    border-radius: 7px;
    padding: 10px;
    opacity: 0.9;
}

#mask{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;left: 0;
    background: #2B2B2B;
    opacity: 0.7;
}
